import React, { useEffect } from 'react'
import AdaptiveColumn from '../../components/Blocks/AdaptiveColumn/AdaptiveColumn'
import AdaptiveFlexBlock from '../../components/Blocks/AdaptiveFlexBlock/AdaptiveFlexBlock'

import styles from './Blog.module.scss'
import topImg from '../../img/blog/blogTopImg.png'
import { connect } from 'react-redux'
import { getNewsThunk } from '../../redux/actions/blogActions'
import NewsDigest from '../../components/NewsDigest/NewsDigest'


const Blog = ({getNewsThunk, news, history, ...props}) => {

    useEffect(() => {
        getNewsThunk()
    }, [getNewsThunk])


    return (
        <div className={styles.Blog}>
                <div className={styles.topImgBlock}>
                 <img className={styles.topImg} src={topImg} alt="topImg"/>
                 <h1>Блог компании</h1>
                </div>

            <AdaptiveFlexBlock className={styles.newsBlock}>
                {
                    news.map(item => {
                        return (
                            <AdaptiveColumn key={item.id} className={styles.newsColumn}>
                                <NewsDigest
                                    item={item}
                                    history={history}
                                />
                            </AdaptiveColumn>
                        )
                    })
                }
            </AdaptiveFlexBlock>


           {/* <div>
            <Button
                text='Добавить новость'
                clickHander={() => history.push('/news-creation')}
            />
           </div> */}
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        news: state.blog.news
    }
}



export default connect(mapStateToProps, {getNewsThunk})(Blog)